.App {
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

html{
  position:relative; 
  min-height: 100%;
}
/*Normalize html and body elements,this style is just good to have*/
html,body{
  margin:0;
  padding:0;
}

.content-wrapper {
  margin-bottom:100px;
}

.logo {
  max-width: 300px;
  height: 100px;
}

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height:100px;
  background-color: #3d3d3d;
  -webkit-box-shadow: 0px 0px 11px 6px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 0px 11px 6px rgba(0,0,0,0.75);
  box-shadow: 0px 0px 11px 6px rgba(0,0,0,0.75);
}

.legal-links-container {
  display: flex;
  flex-direction: row;
  
}

.footer-link {
  text-decoration: none;
  color: #ffffff;
  font-family: 'Roboto', sans-serif;
  font-size: 7px;
  padding-top: 40px;
  padding-left: 40px;
}

.footer-link:hover {
  color: #c20013;
}

.nav-list {
  display: flex;
  justify-content: flex-end;
}

.nav-links {
  list-style: none;
  font-size: 1.5vw;
  padding: 20px;
}

.nav-react-links {
  text-decoration: none;
  color: #566573;
}

.nav-links::after {
  content: '';
  display: block;
  width: 100%;
  height: 3px;
  margin-top: 4px;
  background: #b42525;
  transform: scaleX(0);
  transition: transform .3s;
}

.nav-links:hover::after {
  transform: scaleX(1);
  transition: transform .3s;
}

.nav-react-links:hover {
 font-weight: bold;
}

.NavBar {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.title {
  font-size: 10vw;
  color: #566573;
  font-weight: bold;
}

.title-new {
  font-size: 4vw;
  color: #566573;
  font-weight: bold;
  padding: 5px;
  margin: 15px;
}

input, textarea {
  height: 3vw;
  width: 24vw;
  border: solid 1px #566573;
  outline: none;
  border-radius: 5px;
  opacity: 0.5;
  font-size: 20px;
  padding-left: 15px;
  color: #272a2e;
}

label {
  padding-bottom: 10px;
  font-weight: bold;
  color: #566573;
  font-size: 20px;
}
textarea {
  height: 7vw;
  width: 24vw;
  padding-top: 10px;
}

.contact-form {
  margin-bottom: 200px;;
}

.contact-field {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
}

.submit-button {
	-moz-box-shadow:inset 0px 0px 0px 0px #ffffff;
	-webkit-box-shadow:inset 0px 0px 0px 0px #ffffff;
	box-shadow:inset 0px 0px 0px 0px #ffffff;
	background-color:#ffffff;
	-moz-border-radius:4px;
	-webkit-border-radius:4px;
	border-radius:4px;
	border:2px solid #c20013;
	display:inline-block;
	cursor:pointer;
	color:#5e535e;
	font-family:Arial;
  font-size:16px;
  margin: 25px;
	padding:14px 46px;
	text-decoration:none;
	text-shadow:0px 1px 0px #ffffff;
}
.submit-button:hover {
  background-color:#c20013;
  color: white;
}
.submit-button:active {
	position:relative;
	top:1px;
}

.content-text {
  margin: auto;
  font-size: 20px;
  width: 35vw;
  height: 130px;
}

.home-page {
  display: flex;
  justify-content: center;
  margin-top: 50px;
  margin-bottom: 200px;
}

.team-pic {
  max-width: 35vw;
  height: 40vw;
  border-radius: 5px;
  margin-left: 100px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)
}

.vertical-post {
  color: #b42525;
  font-weight: bold;
  font-size: 25px;
}

@media only screen and (max-width: 700px) {
  .NavBar {
    flex-direction: column;

  }

  .nav-container {
    border-bottom: solid 1px #566573;
    border-top: solid 1px #566573;
    width: 100%;
    padding: 15px;
    margin: 10px;
  }

  .nav-list {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
  }

  .nav-links {
    font-size: 7vw;
    padding: 10px;
  }

  textarea, input {
    height: 10vw;
    width: 50vw;
  }

  .home-text {
    display: flex;
    flex-direction: column;
  }

  .home-page {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .content-text {
    margin: 10px;
    font-size: 20px;
    width: 70vw;
    height: 25vw;
  }

  .team-pic {
    margin: 0;
    width: 100%;
    height: 130vw;
    max-width: 200vw;
  }
}